@media only screen and (max-width: 480px) {
    #header_container{
        padding: 0 0px;
    }
}  
@media only screen and (min-width: 480px) {
    #header_container{
        padding: 0 20px;
    }
}    
@media only screen and (min-width: 768px) {
    #header_container{
        padding: 0 100px;
    }
}   
@media only screen and (min-width: 1600px) {
    #header_container{
        padding: 0 300px;
    }
}   